import axios from "axios";

const apiKey = "68d3c74a18d32691083dc9e313e197f5";
axios.defaults.baseURL = "https://api.themoviedb.org";

export const fetchMoviesTrending = () => {
  return axios
    .get(`/3/trending/all/day?api_key=${apiKey}`)
    .then((resp) => resp.data.results);
};
export const fetchMoviesSearch = (searchQuery, page) => {
  return axios
    .get(
      `/3/search/movie?api_key=${apiKey}&language=en-US&query&query=${searchQuery}&page=${page}`
    )
    .then((resp) => resp.data);
};
export const fetchMoviesDetails = (movieId) => {
  return axios
    .get(`/3/movie/${movieId}?api_key=${apiKey}&language=en-US`)
    .then((resp) => resp.data);
};
export const fetchMoviesCast = (movieId) => {
  return axios
    .get(`/3/movie/${movieId}/credits?api_key=${apiKey}`)
    .then((resp) => resp.data.cast);
};
export const fetchMoviesReviews = (movieId) => {
  return axios
    .get(`/3/movie/${movieId}/reviews?api_key=${apiKey}&language=en-US&page=1`)
    .then((resp) => resp.data.results);
};
export const fetchMoviesGenres = () => {
  return axios
    .get(`/genre/movie/list?api_key=${apiKey}`)
    .then((resp) => resp.data.genres);
};
